<template>
  <div>
    <h2>管理员信息</h2>
    <p>
      <label>管理员ID</label>
      <b>{{id}}</b>
    </p>
    <p>
      <label>账号</label>
      <b>{{account}}</b>
    </p>
    <p>
      <label>用户组</label>
      <b>{{userGroup}}</b>
    </p>
    <p>
      <label>创建时间</label>
      <b>{{getdate(ctime)}}</b>
    </p>
    <p>
      <label>管理员头像</label>

      <el-upload class="avatar-uploader"
                 action="http://127.0.0.1:5000/users/avatar_upload"
                 :show-file-list="false"
                 :on-success="handleAvatarSuccess"
                 :data="upimgid">
        <img v-if="imageUrl"
             :src="imageUrl"
             class="avatar">
        <i v-else
           class="el-icon-plus avatar-uploader-icon"></i>
      </el-upload>

    </p>
  </div>
</template>

<script>
import { accountinfo } from '@/API/apis'
export default {
  data () {
    return {
      id: '',
      account: '',
      userGroup: '',
      ctime: '',
      imageUrl: '',
      upimgid: {},
    }
  },
  created () {
    this.id = localStorage.getItem("id")
    this.upimgid = { id: this.id }
    accountinfo(this.id).then(res => {
      this.account = res.data.accountInfo.account,
        this.userGroup = res.data.accountInfo.userGroup,
        this.ctime = res.data.accountInfo.ctime
      this.imgUrl = res.data.accountInfo.imgUrl
    });
  },
  methods: {
    handleAvatarSuccess (res) {
      if (res.code == 0) {
        //发起bus传值
        this.$bus.$emit('Userheader')

        this.$message({
          message: res.msg,
          type: 'success'
        });
      }
    },
    getdate (ctime) {
      let nDate = new Date(ctime)
      let nYear = nDate.getFullYear()
      let nMonth = nDate.getMonth() + 1
      let nDay = nDate.getDate()
      return ctime = nYear + '/' + nMonth + '/' + nDay
    },

  }
}
</script>

<style lang="less" scoped>
p {
  padding: 20px;
  display: flex;
  justify-content: start;
  align-items: start;
  border-top: 1px solid #ccc;
  label {
    color: #808080;
    display: inline-block;
    width: 90px;
    text-align: right;
    margin-right: 20px;
  }
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409eff;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
}
</style>